___CSS_LOADER_EXPORT___.push([module.id, ".competence-list[data-v-14ea0020]{display:flex;flex-wrap:wrap;gap:20px;justify-content:center}.competence-card[data-v-14ea0020]{background-color:#f9f9f9;border-radius:15px;box-shadow:0 4px 8px rgba(0,0,0,.1);padding:20px;transition:transform .3s ease,box-shadow .3s ease;width:calc(33.33% - 20px)}.competence-card[data-v-14ea0020]:hover{box-shadow:0 8px 16px rgba(0,0,0,.2);transform:translateY(-10px)}.competence-card h2[data-v-14ea0020]{font-size:1.5rem;margin:0 0 10px}.competence-card p[data-v-14ea0020]{font-size:1rem;margin:5px 0}.project-list[data-v-14ea0020]{list-style:none;margin:10px 0;padding:0}.project-list li[data-v-14ea0020]{background:#e3f2fd;border-radius:5px;color:#06c;display:inline-block;font-size:1rem;margin-bottom:5px;padding:5px;text-align:center;width:100%}@media (max-width:768px){.competence-card[data-v-14ea0020]{width:calc(50% - 20px)}}@media (max-width:480px){.competence-card[data-v-14ea0020]{width:100%}}", "",{"version":3,"sources":["webpack://./resources/js/components/layouts/ListeProjetCompetence.vue"],"names":[],"mappings":"AAkEA,kCACA,YAAA,CACA,cAAA,CACA,QAAA,CACA,sBACA,CAEA,kCACA,wBAAA,CAEA,kBAAA,CAEA,mCAAA,CAHA,YAAA,CAIA,iDAAA,CAFA,yBAGA,CAEA,wCAEA,oCAAA,CADA,2BAEA,CAEA,qCAEA,gBAAA,CADA,eAEA,CAEA,oCAEA,cAAA,CADA,YAEA,CAEA,+BACA,eAAA,CAEA,aAAA,CADA,SAEA,CAEA,kCAGA,kBAAA,CAGA,iBAAA,CAJA,UAAA,CAKA,oBAAA,CANA,cAAA,CAGA,iBAAA,CACA,WAAA,CAIA,iBAAA,CADA,UAEA,CAEA,yBACA,kCACA,sBACA,CACA,CAEA,yBACA,kCACA,UACA,CACA","sourcesContent":["<template>\n  <div class=\"competence-list\">\n    <div class=\"competence-card\" v-for=\"competence in competences\" :key=\"competence.id\">\n      <h2>{{ competence.titre }}</h2>\n      <p>{{ competence.description_competence }}</p>\n\n      <h3>Projets Associés :</h3>\n      <ul class=\"project-list\">\n        <li v-for=\"projet in getProjetsForCompetence(competence)\" :key=\"projet.id\">\n          {{ projet.name }}\n        </li>\n      </ul>\n      <a class=\"btn btn-primary\" :href=\"`/fiche_competence/${competence.id}`\">Plus de détails...</a>\n    </div>\n  </div>\n</template>\n\n<script>\nimport axios from 'axios';\n\nexport default {\n  data() {\n    return {\n      competences: [], // Tableau pour stocker les compétences\n      projets: [] // Tableau pour stocker les projets\n    };\n  },\n\n  methods: {\n    // Méthode pour récupérer les compétences\n    async fetchCompetences() {\n      try {\n        const response = await axios.get('/gestion/competences/get');\n        this.competences = response.data; // Stocke les compétences récupérées dans le tableau\n      } catch (error) {\n        console.error(\"Erreur lors de la récupération des compétences:\", error);\n      }\n    },\n\n    // Méthode pour récupérer les projets\n    async fetchProjets() {\n      try {\n        const response = await axios.get('/gestion/projets/get');\n        this.projets = response.data; // Stocke les projets récupérés dans le tableau\n      } catch (error) {\n        console.error(\"Erreur lors de la récupération des projets:\", error);\n      }\n    },\n\n    // Récupère les projets associés à une compétence\n    getProjetsForCompetence(competence) {\n      return this.projets.filter(projet => \n        projet.competence_id.includes(competence.id)\n      );\n    }\n  },\n\n  // Méthode pour récupérer les données au moment du montage du composant\n  async mounted() {\n    await this.fetchCompetences();\n    await this.fetchProjets();\n  }\n};\n</script>\n\n<style scoped>\n.competence-list {\n  display: flex;\n  flex-wrap: wrap;\n  gap: 20px;\n  justify-content: center;\n}\n\n.competence-card {\n  background-color: #f9f9f9;\n  padding: 20px;\n  border-radius: 15px;\n  width: calc(33.33% - 20px); /* Chaque carte prend 1/3 de la largeur moins l'espace entre les cartes */\n  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);\n  transition: transform 0.3s ease, box-shadow 0.3s ease;\n}\n\n.competence-card:hover {\n  transform: translateY(-10px);\n  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);\n}\n\n.competence-card h2 {\n  margin: 0 0 10px;\n  font-size: 1.5rem;\n}\n\n.competence-card p {\n  margin: 5px 0;\n  font-size: 1rem;\n}\n\n.project-list {\n  list-style: none;\n  padding: 0;\n  margin: 10px 0;\n}\n\n.project-list li {\n  font-size: 1rem;\n  color: #0066cc;\n  background: #e3f2fd;\n  margin-bottom: 5px;\n  padding: 5px;\n  border-radius: 5px;\n  display: inline-block;\n  width: 100%;\n  text-align: center;\n}\n\n@media (max-width: 768px) {\n  .competence-card {\n    width: calc(50% - 20px); /* Sur les écrans plus petits, les cartes occupent 50% de la largeur */\n  }\n}\n\n@media (max-width: 480px) {\n  .competence-card {\n    width: 100%; /* Sur les très petits écrans, chaque carte occupe toute la largeur */\n  }\n}\n</style>\n"],"sourceRoot":""}]);
