___CSS_LOADER_EXPORT___.push([module.id, ".project-list[data-v-f09080ea]{display:flex;flex-wrap:wrap;gap:30px;justify-content:center}.project-card[data-v-f09080ea]{background-color:#f9f9f9;border:1px solid #ccc;border-radius:15px;box-shadow:0 4px 8px rgba(0,0,0,.1);padding:20px;transition:transform .3s ease,box-shadow .3s ease;width:300px}.project-card[data-v-f09080ea]:hover{box-shadow:0 8px 16px rgba(0,0,0,.2);transform:translateY(-10px)}.project-title[data-v-f09080ea]{color:#333;font-size:1.5rem;font-weight:700;margin:0 0 10px;text-align:center}.project-description[data-v-f09080ea]{color:#666;font-size:1rem;margin:10px 0;text-align:justify}.project-date[data-v-f09080ea]{color:#333;font-size:1rem;margin-top:10px;text-align:center}.competences-section[data-v-f09080ea]{margin-top:15px}.competence-list[data-v-f09080ea]{list-style:none;margin:0;padding:0}.competence-item[data-v-f09080ea]{background:#e3f2fd;border:1px solid #4caf50;border-radius:5px;color:#06c;display:inline-block;font-size:1rem;margin-bottom:8px;padding:8px;text-align:center;width:100%}", "",{"version":3,"sources":["webpack://./resources/js/components/layouts/ListeProjet.vue"],"names":[],"mappings":"AAyGA,+BACA,YAAA,CACA,cAAA,CACA,QAAA,CACA,sBACA,CAEA,+BACA,wBAAA,CAMA,qBAAA,CAJA,kBAAA,CAEA,mCAAA,CAHA,YAAA,CAIA,iDAAA,CAFA,WAIA,CAEA,qCAEA,oCAAA,CADA,2BAEA,CAEA,gCAGA,UAAA,CADA,gBAAA,CAEA,eAAA,CAHA,eAAA,CAIA,iBACA,CAEA,sCAGA,UAAA,CADA,cAAA,CADA,aAAA,CAGA,kBACA,CAEA,+BAGA,UAAA,CADA,cAAA,CADA,eAAA,CAGA,iBACA,CAEA,sCACA,eACA,CAEA,kCACA,eAAA,CAEA,QAAA,CADA,SAEA,CAEA,kCAGA,kBAAA,CAOA,wBAAA,CAJA,iBAAA,CAJA,UAAA,CAKA,oBAAA,CANA,cAAA,CAGA,iBAAA,CACA,WAAA,CAIA,iBAAA,CADA,UAGA","sourcesContent":["<template>\n  <div class=\"project-list\">\n    <div class=\"project-card\" v-for=\"projet in sortedProjets\" :key=\"projet.id\">\n      <h2 class=\"project-title\">{{ projet.name }}</h2>\n      <div class=\"project-description\" v-html=\"projet.description\"></div>\n\n      <!-- Affichage de la date de création -->\n      <div class=\"project-date\">\n        <p>Créé le: {{ formatDate(projet.created_at) }}</p>\n      </div>\n      \n      <!-- Affichage des compétences -->\n      <div class=\"competences-section\">\n        <h3>Compétences Associées:</h3>\n        <ul class=\"competence-list\">\n          <li v-for=\"competence in getCompetencesForProject(projet)\" :key=\"competence.id\" class=\"competence-item\">\n            {{ competence.titre }}\n          </li>\n        </ul>\n        <a class=\"btn btn-primary\" :href=\"`/fiche_projet/${projet.id}`\">Plus de détails...</a>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nimport axios from \"axios\";\n\nexport default {\n  data() {\n    return {\n      projets: [], // Liste des projets\n      competences: [] // Liste des compétences\n    };\n  },\n\n  methods: {\n    // Méthode pour récupérer les projets\n    async fetchProjets() {\n      try {\n        const response = await axios.get(\"/gestion/projets/get\");\n        this.projets = response.data.map(projet => ({\n          ...projet,\n          competence_id: Array.isArray(projet.competence_id)\n            ? projet.competence_id\n            : projet.competence_id\n                ?.replace(/[{}]/g, \"\")\n                .split(\",\")\n                .filter(id => id) // Retirer les valeurs nulles ou vides\n                .map(id => parseInt(id, 10)) || [], // Convertir en tableau de nombres\n          created_at: projet.created_at ? new Date(projet.created_at) : new Date() // Assurer que created_at soit une date valide\n        }));\n      } catch (error) {\n        console.error(\"Erreur lors de la récupération des projets :\", error);\n      }\n    },\n\n    // Méthode pour récupérer les compétences\n    async fetchCompetences() {\n      try {\n        const response = await axios.get(\"/gestion/competences/get\");\n        this.competences = response.data;\n      } catch (error) {\n        console.error(\"Erreur lors de la récupération des compétences :\", error);\n      }\n    },\n\n    // Récupère les compétences associées à un projet\n    getCompetencesForProject(projet) {\n      return projet.competence_id\n        .map(id => this.competences.find(competence => competence.id === id))\n        .filter(competence => competence); // Exclure les compétences non trouvées\n    },\n\n    // Fonction pour formater la date\n    formatDate(date) {\n      const options = {\n        weekday: \"short\",\n        year: \"numeric\",\n        month: \"2-digit\",\n        day: \"2-digit\",\n        hour: \"2-digit\",\n        minute: \"2-digit\",\n        second: \"2-digit\",\n      };\n      return new Date(date).toLocaleDateString(\"fr-FR\", options);\n    }\n  },\n\n  // Récupère les données au montage du composant\n  async mounted() {\n    await this.fetchCompetences();\n    await this.fetchProjets();\n  },\n\n  computed: {\n    // Tri des projets par date de création\n    sortedProjets() {\n      return this.projets.sort((a, b) => b.created_at - a.created_at);\n    }\n  }\n};\n</script>\n\n<style scoped>\n.project-list {\n  display: flex;\n  flex-wrap: wrap;\n  gap: 30px;\n  justify-content: center;\n}\n\n.project-card {\n  background-color: #f9f9f9;\n  padding: 20px;\n  border-radius: 15px;\n  width: 300px;\n  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);\n  transition: transform 0.3s ease, box-shadow 0.3s ease;\n  border: 1px solid #ccc;\n}\n\n.project-card:hover {\n  transform: translateY(-10px);\n  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);\n}\n\n.project-title {\n  margin: 0 0 10px;\n  font-size: 1.5rem;\n  color: #333;\n  font-weight: bold;\n  text-align: center;\n}\n\n.project-description {\n  margin: 10px 0;\n  font-size: 1rem;\n  color: #666;\n  text-align: justify;\n}\n\n.project-date {\n  margin-top: 10px;\n  font-size: 1rem;\n  color: #333;\n  text-align: center;\n}\n\n.competences-section {\n  margin-top: 15px;\n}\n\n.competence-list {\n  list-style: none;\n  padding: 0;\n  margin: 0;\n}\n\n.competence-item {\n  font-size: 1rem;\n  color: #0066cc;\n  background: #e3f2fd;\n  margin-bottom: 8px;\n  padding: 8px;\n  border-radius: 5px;\n  display: inline-block;\n  width: 100%;\n  text-align: center;\n  border: 1px solid #4caf50;\n}\n</style>\n"],"sourceRoot":""}]);
